<template>
	
	<view>
		<skeleton v-if="showSkeleton" ref="skeleton" loading="chiaroscuro" selector="skeleton" bgcolor="#FFF"></skeleton>
		<view class="skeleton">
				<!-- 状态栏 -->
				<view class="status" :style="{ position: headerPosition,top:statusTop,opacity: afterHeaderOpacity}"></view>
				<!-- 顶部导航栏 -->
				<view class="header skeleton-rect" :style="{ position: headerPosition,top:headerTop,opacity: afterHeaderOpacity }">
					<!-- 定位城市 -->
					<view class="addr">
						<view class="icon location"></view>
						{{ city }}
					</view>
					<!-- 搜索框 -->
					<view class="input-box">
							<text style="color: #C0C0C0;font-size: 28upx;padding-left: 5px;width: 100%;" @tap="toSearch()">搜索</text>
		<!-- 				<input
							placeholder="默认关键字"
							placeholder-style="color:#c0c0c0;"
							@tap="toSearch()"
						/> -->
						<view class="icon search"></view>
					</view>
					<!-- 右侧图标按钮 -->
					<view class="icon-btn">
						<view class="icon yuyin"></view>
						<view class="icon tongzhi"></view>
					</view>
				</view>
				<!-- 占位 -->
				<view class="place"></view>
				<!-- 轮播图 -->
				<view class="swiper skeleton-rect">
					<view class="swiper-box">
						<swiper circular="true" autoplay="true" @change="swiperChange">
							<swiper-item v-for="swiper in swiperList" :key="swiper.id">
								<image :src="swiper.img" @tap="toSwiper(swiper)"></image>
							</swiper-item>
						</swiper>
						<view class="indicator">
							<view
								class="dots"
								v-for="(swiper, index) in swiperList"
								:class="[currentSwiper >= index ? 'on' : '']"
								:key="index"
							></view>
						</view>
					</view>
				</view>
				<!-- 分类列表 -->
				<view class="category-list">
					<view
						class="category skeleton-radius"
						v-for="(row, index) in categoryList"
						:key="index"
						@tap="toCategory(row)"
					>
						<view class="img"><image :src="row.img"></image></view>
						<view class="text">{{ row.name }}</view>
					</view>
				</view>
				<!-- 广告图 -->
				<view class="banner skeleton-rect"><image src="../../static/img/banner.jpg"></image></view>
				<!-- 活动区 -->
				<view class="promotion">
					<view class="text">优惠专区</view>
					<view class="list">
						<view
							class="column skeleton-rect"
							v-for="(row, index) in Promotion"
							@tap="toPromotion(row)"
							:key="index"
						>
							<view class="top">
								<view class="title">{{ row.title }}</view>
								<view class="countdown" v-if="row.countdown">
									<view>{{ row.countdown.h }}</view>
									:
									<view>{{ row.countdown.m }}</view>
									:
									<view>{{ row.countdown.s }}</view>
								</view>
							</view>
							<view class="left">
								<view class="ad">{{ row.ad }}</view>
								<view class="into">点击进入</view>
							</view>
							<view class="right"><image :src="row.img"></image></view>
						</view>
					</view>
				</view>
				<!-- 商品列表 -->
				<view class="list-title">
					<image src="../../static/img/hua.png"></image>
					猜你喜欢
					<image src="../../static/img/hua.png"></image>
				</view>
				<view class="xy-product-list">
					<view class="xy-product" :class="'product-'+index" 
					v-for="(product,index) in productList" 
					:key="product.goods_id" 
					@tap="toGoods(product)">
						<image class="xy-product-img" mode="widthFix" :src="product.img"></image>
						<view class="xy-product-name">{{product.name}}</view>
						<view class="xy-product-info">
							<view class="xy-product-price">{{product.price}}</view>
							<view class="xy-product-slogan">{{product.slogan}}</view>
						</view>
					</view>
				</view>
				<view class="xy-loading-text">{{loadingText}}</view>

				</view>
			</view>
	</view>
</template>

<script>
//高德SDK
import amap from '@/common/SDK/amap-wx.js';
import skeleton from '@/components/quick-skeleton/quick-skeleton.vue'
export default {
	components:{skeleton},
	data() {
		return {
			showSkeleton:true,//骨架屏
			productName:'product',
			afterHeaderOpacity: 1,//不透明度
			headerPosition: 'fixed',
			headerTop:null,
			statusTop:null,
			city: '北京',
			currentSwiper: 0,
			// 轮播图片
			swiperList: [
			],
			// 分类菜单
			categoryList: [
				{ id: 1, name: '办公', img: '../../static/img/category/1.png' },
				{ id: 2, name: '家电', img: '../../static/img/category/2.png' },
				{ id: 3, name: '服饰', img: '../../static/img/category/3.png' },
				{ id: 4, name: '日用', img: '../../static/img/category/4.png' },
				{ id: 5, name: '蔬果', img: '../../static/img/category/5.png' },
				{ id: 6, name: '运动', img: '../../static/img/category/6.png' },
				{ id: 7, name: '书籍', img: '../../static/img/category/7.png' },
				{ id: 8, name: '文具', img: '../../static/img/category/8.png' }
			],
			Promotion: [],
			//猜你喜欢列表
			productList: [
				{
					goods_id: 0,
					img: '../../static/img/goods/p1.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 1,
					img: '../../static/img/goods/p2.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 2,
					img: '../../static/img/goods/p3.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 3,
					img: '../../static/img/goods/p4.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 4,
					img: '../../static/img/goods/p5.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 5,
					img: '../../static/img/goods/p6.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 6,
					img: '../../static/img/goods/p7.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 7,
					img: '../../static/img/goods/p8.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 8,
					img: '../../static/img/goods/p9.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				},
				{
					goods_id: 9,
					img: '../../static/img/goods/p10.jpg',
					name: '商品名称商品名称商品名称商品名称商品名称',
					price: '￥168',
					slogan: '1235人付款'
				}
			],
			loadingText: '正在加载...'
		};
	},
	onPageScroll(e) {
		//兼容iOS端下拉时顶部漂移
		this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
		this.headerTop = e.scrollTop>=0?null:0;
		this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		uni.showToast({ title: '触发上拉加载' });
		let len = this.productList.length;
		if (len >= 40) {
			this.loadingText = '到底了';
			return false;
		}
		// 演示,随机加入商品,生成环境请替换为ajax请求
		let end_goods_id = this.productList[len - 1].goods_id;
		let list = {
			"feedList[0]":{
				name:'测试'
			}
		}
		for (let i = 1; i <= 10; i++) {
			let goods_id = end_goods_id + i;
			let p = {
				goods_id: goods_id,
				img:
					'../../static/img/goods/p' + (goods_id % 10 == 0 ? 10 : goods_id % 10) + '.jpg',
				name: '商品名称商品名称商品名称商品名称商品名称',
				price: '￥168',
				slogan: '1235人付款'
			};
			this.productList.push(p);
		}
	},
	onLoad() {
		

	},
	onReady() {
		const that = this;
		
		that.$refs.skeleton.attachedAction();
		
		that.$refs.skeleton.readyAction();		
		setTimeout(() =>{
			that.showSkeleton = false;
			// #ifdef APP-PLUS
			this.statusHeight = plus.navigator.getStatusbarHeight();
			// #endif
			this.amapPlugin = new amap.AMapWX({
				//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
				key: '04cff6663be3cb476b9915914506122e'
			});
			//定位地址
			this.amapPlugin.getRegeo({
				success: data => {
					this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉
				},
			  fail: info => {
				//失败回调
				console.log(info)
			  }
			});
			//开启定时器
			this.Timer();
			this.swiperList = [//为了骨架屏 把他放到这里再结束
				{ id: 1, src: 'url1', img: '../../static/img/1.jpg' },
				{ id: 2, src: 'url2', img: '../../static/img/2.jpg' },
				{ id: 3, src: 'url3', img: '../../static/img/3.jpg' }];
			//加载活动专区
			this.loadPromotion();
		},3000);
	},
	methods: {
		tranProduct(index){
			return `product-${index}`;
		},
		
		//加载Promotion 并设定倒计时,,实际应用中应该是ajax加载此数据。
		loadPromotion() {
			let cutTime = new Date();
			let yy = cutTime.getFullYear(),
				mm = cutTime.getMonth() + 1,
				dd = cutTime.getDate();
			let tmpcountdown = yy + '/' + mm + '/' + dd + ' 23:59:59';
			let tmpPromotion = [
				{
					title: '整点秒杀',
					ad: '整天秒杀专区',
					img: '../../static/img/s1.jpg',
					countdown: false
				},
				{
					title: '限时抢购',
					ad: '每天23点上线',
					img: '../../static/img/s2.jpg',
					countdown: tmpcountdown
				} //countdown为目标时间，程序会获取当前时间倒数
			];
			//检查倒计时
			for (let i = 0; i < tmpPromotion.length; i++) {
				let row = tmpPromotion[i];
				if (row.countdown) {
					let h = '00',
						m = '00',
						s = '00';
					let currentTime = new Date();
					let cutoffTime = new Date(tmpcountdown);
					if (!(currentTime >= cutoffTime)) {
						let countTime = parseInt(
							(cutoffTime.getTime() - currentTime.getTime()) / 1000
						);
						h = parseInt(countTime / 3600);
						m = parseInt((countTime % 3600) / 60);
						s = countTime % 60;
						h = h < 10 ? '0' + h : h;
						m = m < 10 ? '0' + m : m;
						s = s < 10 ? '0' + s : s;
					}
					tmpPromotion[i].countdown = { h: h, m: m, s: s };
				}
			}
			this.Promotion = tmpPromotion;
		},
		//定时器
		Timer() {
			setInterval(() => {
				if (this.Promotion.length > 0) {
					for (let i = 0; i < this.Promotion.length; i++) {
						let row = this.Promotion[i];
						if (row.countdown) {
							if (
								!(
									row.countdown.h == 0 &&
									row.countdown.m == 0 &&
									row.countdown.s == 0
								)
							) {
								if (row.countdown.s > 0) {
									row.countdown.s--;
									row.countdown.s =
										row.countdown.s < 10
											? '0' + row.countdown.s
											: row.countdown.s;
								} else if (row.countdown.m > 0) {
									row.countdown.m--;
									row.countdown.m =
										row.countdown.m < 10
											? '0' + row.countdown.m
											: row.countdown.m;
									row.countdown.s = 59;
								} else if (row.countdown.h > 0) {
									row.countdown.h--;
									row.countdown.h =
										row.countdown.h < 10
											? '0' + row.countdown.h
											: row.countdown.h;
									row.countdown.m = 59;
									row.countdown.s = 59;
								}
								this.Promotion[i].countdown = row.countdown;
							}
						}
					}
				}
			}, 1000);
		},
		//搜索跳转
		toSearch() {
			uni.navigateTo({
				url: '../../goodPage/search/searchView'
			});
		},
		//轮播图跳转
		toSwiper(e) {
			uni.showToast({ title: e.src, icon: 'none' });
		},
		//分类跳转
		toCategory(e) {
			//uni.showToast({title: e.name,icon:"none"});
			uni.navigateTo({
				url: '../../goodPage/goods/goods-list?cid=' + e.id + '&name=' + e.name
			});
		},
		//推荐商品跳转
		toPromotion(e) {
			uni.showToast({ title: e.title, icon: 'none' });
		},
		//商品跳转
		toGoods(e) {
			uni.showToast({ title: '商品' + e.goods_id, icon: 'none' });
			uni.navigateTo({
				url: '../../goodPage/goods/goods'
			});
		},
		//轮播图指示器
		swiperChange(event) {
			this.currentSwiper = event.detail.current;
		}
	}
};
</script>
<style lang="scss">
page{position: relative;}
@font-face {
	font-family: 'HMfont-home';
	src: url('data:application/x-font-woff2;charset=utf-8;base64,')
		format('woff2');
}
.icon {
	font-family: 'HMfont-home' !important;
	font-size: 60upx;
	font-style: normal;
	color: #000000;
	&.yuyin {
		&:before {
			content: '\e64e';
		}
	}
	&.tongzhi {
		&:before {
			content: '\e729';
		}
	}
	&.search {
		&:before {
			content: '\e628';
		}
	}
	&.location {
		&:before {
			content: '\e611';
		}
	}
	&.xia {
		&:before {
			content: '\e689';
		}
	}
}
.pullDown-effects{
	position: fixed;
	//top: calc(100upx - 36vw);
	top: 0;
	z-index: 9;
	width: 100%;
	height: 36vw;
	/*  #ifdef  APP-PLUS  */
	padding-top: var(--status-bar-height);
	/*  #endif  */
	image{
		width: 100%;
		height: 36vw;
	}
}
.status {
	width: 100%;
	height: 0;
	position: fixed;
	z-index: 10;
	background-color: #fff;
	top: 0;
	/*  #ifdef  APP-PLUS  */
	height: var(--status-bar-height); //覆盖样式
	/*  #endif  */
}
.header {
	width: 92%;
	padding: 0 4%;
	height: 100upx;
	display: flex;
	align-items: center;
	position: fixed;
	top: 0;
	z-index: 10;
	background-color: #fff;

	/*  #ifdef  APP-PLUS  */
	top: var(--status-bar-height);
	/*  #endif  */

	.addr {
		width: 120upx;
		height: 60upx;
		flex-shrink: 0;
		display: flex;
		align-items: center;
		font-size: 28upx;
		.icon {
			height: 60upx;
			margin-right: 5upx;
			display: flex;
			align-items: center;
			font-size: 42upx;
			color: #ffc50a;
		}
	}
	.input-box {
		width: 100%;
		height: 60upx;
		background-color: #f5f5f5;
		border-radius: 30upx;
		position: relative;
		display: flex;
		align-items: center;
		.icon {
			display: flex;
			align-items: center;
			position: absolute;
			top: 0;
			right: 0;
			width: 60upx;
			height: 60upx;
			font-size: 34upx;
			color: #c0c0c0;
		}
		input {
			padding-left: 28upx;
			height: 28upx;
			font-size: 28upx;
		}
	}
	.icon-btn {
		width: 120upx;
		height: 60upx;
		flex-shrink: 0;
		display: flex;
		.icon {
			width: 60upx;
			height: 60upx;
			display: flex;
			justify-content: flex-end;
			align-items: center;
			font-size: 42upx;
		}
	}
}
.place {
	background-color: #ffffff;
	height: 100upx;
	/*  #ifdef  APP-PLUS  */
	margin-top: var(--status-bar-height);
	/*  #endif  */
}
.swiper {
	width: 100%;
	margin-top: 10upx;
	display: flex;
	justify-content: center;
	.swiper-box {
		width: 92%;
		height: 30.7vw;

		overflow: hidden;
		border-radius: 15upx;
		box-shadow: 0upx 8upx 25upx rgba(0, 0, 0, 0.2);
		//兼容ios，微信小程序
		position: relative;
		z-index: 1;
		swiper {
			width: 100%;
			height: 30.7vw;
			swiper-item {
				image {
					width: 100%;
					height: 30.7vw;
				}
			}
		}
		.indicator {
			position: absolute;
			bottom: 20upx;
			left: 20upx;
			background-color: rgba(255, 255, 255, 0.4);
			width: 150upx;
			height: 5upx;
			border-radius: 3upx;
			overflow: hidden;
			display: flex;
			.dots {
				width: 100%;
				&.on {
					background-color: rgba(255, 255, 255, 1);
				}
			}
		}
	}
}

.category-list {
	width: 92%;
	margin: 0 4%;
	padding: 0 0 30upx 0;
	border-bottom: solid 2upx #f6f6f6;
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	.category {
		width: 25%;
		margin-top: 50upx;
		display: flex;
		flex-wrap: wrap;
		.img {
			width: 100%;
			display: flex;
			justify-content: center;
			image {
				width: 9vw;
				height: 9vw;
			}
		}
		.text {
			margin-top: 16upx;
			width: 100%;
			display: flex;
			justify-content: center;
			font-size: 24upx;
			color: #3c3c3c;
		}
	}
}
.banner {
	width: 92%;
	margin: 40upx 4%;
	image {
		width: 100%;
		height: 20vw;
		border-radius: 10vw;
		box-shadow: 0upx 5upx 25upx rgba(0, 0, 0, 0.3);
	}
}
.promotion {
	width: 92%;
	margin: 0 4%;
	.text {
		width: 100%;
		height: 60upx;
		font-size: 34upx;
		font-weight: 600;
		margin-top: -10upx;
	}
	.list {
		width: 100%;
		display: flex;
		justify-content: space-between;
		.column {
			width: 43%;
			padding: 15upx 3%;
			background-color: #ebf9f9;
			border-radius: 10upx;
			overflow: hidden;
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			.top {
				width: 100%;
				height: 40upx;
				display: flex;
				align-items: center;
				margin-bottom: 5upx;
				.title {
					font-size: 30upx;
				}
				.countdown {
					margin-left: 20upx;
					display: flex;
					height: 40upx;
					display: flex;
					align-items: center;
